// 
const ENCRYPTION_KEY = 'simple-shell-encryption-key-12345'; // 
const ENCRYPTION_ALGORITHM = 'aes-256-cbc';
const IV_LENGTH = 16; //  aes-256-cbcIV16

// AI worker
let aiWorker = null;
let aiRequestMap = new Map();
let nextRequestId = 1;

