  // APICORS
  ipcMain.handle('ai:sendAPIRequest', async (event, requestData, isStream) => {
    try {
      // 
      if (!requestData.url || !requestData.apiKey || !requestData.model || !requestData.messages) {
        throw new Error('');
      }

      console.log(`${isStream ? '' : ''}API: ${requestData.url}`);

      if (isStream) {
        // 
        const https = require('https');
        const http = require('http');
        const url = new URL(requestData.url);
        
        const requestModule = url.protocol === 'https:' ? https : http;
        
        const options = {
          method: 'POST',
          hostname: url.hostname,
          path: url.pathname + url.search,
          port: url.port || (url.protocol === 'https:' ? 443 : 80),
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${requestData.apiKey}`
          }
        };
        
        const req = requestModule.request(options, (res) => {
          if (res.statusCode !== 200) {
            event.sender.send('stream-error', `API: ${res.statusCode} ${res.statusMessage}`);
            return;
          }
          
          res.on('data', (chunk) => {
            try {
              const data = chunk.toString('utf-8');
              const lines = data.split('\n');
              
              for (const line of lines) {
                if (line.startsWith('data: ') && line !== 'data: [DONE]') {
                  try {
                    const jsonData = JSON.parse(line.substring(6));
                    if (jsonData.choices && jsonData.choices[0] && jsonData.choices[0].delta && jsonData.choices[0].delta.content) {
                      event.sender.send('stream-chunk', jsonData.choices[0].delta.content);
                    }
                  } catch (e) {
                    // JSON
                    console.log('JSON:', line);
                  }
                }
              }
            } catch (error) {
              console.error(':', error);
            }
          });
          
          res.on('end', () => {
            event.sender.send('stream-end', '');
          });
        });
        
        req.on('error', (error) => {
          console.error(':', error);
          event.sender.send('stream-error', error.message);
        });
        
        // 
        req.write(JSON.stringify({
          model: requestData.model,
          messages: requestData.messages,
          stream: true
        }));
        
        req.end();
        
        return { success: true, message: '' };
      } else {
        // 
        return new Promise((resolve, reject) => {
          try {
            const https = require('https');
            const http = require('http');
            const url = new URL(requestData.url);
            
            const requestModule = url.protocol === 'https:' ? https : http;
            
            const options = {
              method: 'POST',
              hostname: url.hostname,
              path: url.pathname + url.search,
              port: url.port || (url.protocol === 'https:' ? 443 : 80),
              headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${requestData.apiKey}`
              }
            };
            
            const req = requestModule.request(options, (res) => {
              let responseData = '';
              
              // 200
              if (res.statusCode !== 200) {
                resolve({ 
                  success: false, 
                  error: `API: ${res.statusCode} ${res.statusMessage}`
                });
                return;
              }
              
              res.on('data', (chunk) => {
                responseData += chunk.toString('utf-8');
              });
              
              res.on('end', () => {
                try {
                  // JSON
                  const data = JSON.parse(responseData);
                  if (data.choices && data.choices[0] && data.choices[0].message && data.choices[0].message.content) {
                    resolve({ 
                      success: true, 
                      content: data.choices[0].message.content 
                    });
                  } else {
                    resolve({
                      success: false,
                      error: 'API',
                      rawResponse: responseData
                    });
                  }
                } catch (error) {
                  console.error('API:', error);
                  resolve({ 
                    success: false, 
                    error: `: ${error.message}`,
                    rawResponse: responseData.substring(0, 200) + '...'
                  });
                }
              });
            });
            
            req.on('error', (error) => {
              console.error(':', error);
              resolve({ 
                success: false, 
                error: `: ${error.message}` 
              });
            });
            
            // 
            req.write(JSON.stringify({
              model: requestData.model,
              messages: requestData.messages,
              stream: false
            }));
            
            req.end();
          } catch (error) {
            console.error(':', error);
            resolve({ 
              success: false, 
              error: `: ${error.message}` 
            });
          }
        });
      }
    } catch (error) {
      console.error('API:', error);
      return { 
        success: false, 
        error: error.message 
      };
    }
  });

